@mixin big-rectangle{
    width: 184px;
    height: 139px;
    background-color: white;
    border-radius: 20px;
    margin-right: 2px;
    margin-left: 20px;
}

@mixin small-rectangle{
    width: 184px;
    height: 103px;
    background-color: white;
    border-radius: 20px;
    margin-right: 2px;
    margin-top: 20px;
    margin-left: 20px;
}